
.container-do-container{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container{
    box-shadow: 3px 3px 10px;
    background-color: rgb(231, 237, 238);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 5px;
    padding-top: 25px;
    padding-bottom: 25px;
    width: 30%;
    border-radius: 15px;
}

.inputs{
    text-decoration: none;
    padding: 15px;
    border: 1px solid black;
    border-radius: 8px;
    background-color: rgb(231, 237, 238);
    position: relative;
    font-family: 'raleway', sans-serif;
    width: 200px;
}
.inputs:focus{
outline: none;
}

p{
    font-family: 'Roboto' , sans-serif;
    opacity: 0.6;
    text-align: center;
    letter-spacing: 0.05em;
    font-size: 15px;
}


@media screen and (max-width: 500px) { 

    .inputs{

        width: 60%

    }
}
