h1,h2{
    font-family: "Raleway" , sans-serif;
    font-size: 32px;
    text-align: center;
    margin-right: 10px;
    color: #302f31
}

.container-geral{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-container{
    background-color: rgb(231, 237, 238);
    width: 30%;
    padding: 25px;
    border-radius: 15px;
    box-shadow: 3px 3px 10px;
}

.centraliza-conteudo{
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-top: 100px;

}

.div-email{
    display: flex;
    justify-content: center;
}

.div-password{
    display: flex;
    justify-content: center;
    margin-top: 25px;
}

.input{
    text-decoration: none;
    padding: 15px;
    border: 1px solid black;
    border-radius: 8px;
    background-color: rgb(231, 237, 238);
    position: relative;
    font-family: 'raleway', sans-serif;
    font-weight: 00;
}

.input:focus{
outline: none;
border: 2px solid black;
padding: 14PX;
}

#label-senha{
    position: absolute;
    margin-left: 100px;
    margin-top: -78px;
    background-color: rgb(231, 237, 238);
    padding-left: 5px;
    padding-right: 5px;
    border-radius: 3px;
    font-family: 'raleway', sans-serif;
    font-weight: 600;
    font-size: 10px;
}

#label-email{
    position: absolute;
    margin-left: 100px;
    margin-top: -151px;
    background-color: rgb(231, 237, 238);
    padding-left: 5px;
    padding-right: 5px;
    font-family: 'raleway', sans-serif;
    font-weight: 600;
    font-size: 10px;
}

.lembrar-senha{
    display: flex;
    justify-content: center;
    font-family: 'Raleway' , sans-serif;
    margin-right: 35px;
    margin-top: 10px;
    font-size: 13px;
    gap: 5px;
    text-decoration: underline;
}

#lembrar-senha-label{
    cursor: pointer;
}

.icon-container {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.icon{
    width: 75px;
    position: absolute;
    margin-top: 50px;
    cursor: pointer;
}

.userName{
    position: absolute;
    margin-top: 140px;
    font-size: 12px;
    font-family: "Raleway" , sans-serif;
}

@media screen and (max-width: 800px) { 

    .input{
        width: 100%;
    }

    #label-senha{
        display: none;
    }
    #label-email{
        display: none;
    }

}