.btn-container{
    display: flex;
    justify-content: center;
    padding-top: 10px;
}

#btn-confirma{
    padding: 10px;
    border: none;
    border-radius: 8px;
    background-color:#d436e9;

    color: white;
    cursor: pointer;
    padding-left: 30px;
    padding-right: 30px;
}

#btn-confirma:hover{
    padding: 10px;
    border: none;
    border-radius: 8px;
    background-color:#d436e9da;
    color: white;
    padding-left: 30px;
    padding-right: 30px;
}