.container-main{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.img-container{
    background-color: rgb(231, 237, 238);
    width: 30%;
    border-radius: 10px;
    margin: 0 auto;
    box-shadow: 3px 3px 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 25px;
    padding-bottom: 25px;
}

img{
    max-width: 300px;
}

@media screen and (max-width: 700px) { 

    img{
        width: 100%;
    }
}